<template>
  <div class="acoount bgfff">
    <h2 class="title">计费管理</h2>
    <div class="acoount-content">
      <div class="account-top">
        续费凭证：
        <label class="upload" for="fileInput">
          <i class="el-icon-plus"></i>
          上传凭证
        </label>
        <input v-show="false" type="file" @change="tirggerFile($event)" id="fileInput" />
      </div>
      <div class="acoount-item">
        <span>企业名称</span>
        <span>{{initModel.name}}</span>
      </div>
      <div class="acoount-item">
        <span>硬件编号</span>
        <span>{{initModel.code}}</span>
      </div>
      <div class="acoount-item" v-if="fileData.chargeValue">
        <span>计费模式</span>
        <span>{{fileData.chargeType===0?"按次":"按时间"}}</span>
      </div>
      <div class="acoount-item" v-if="fileData.chargeValue">
        <span v-if="fileData.chargeType===0">续费：次</span>
        <span v-else>续费：小时</span>
        <span>{{fileData.chargeValue||"--"}}</span>
      </div>
      <div class="acoount-item" v-if="fileData.chargeValue">
        <span>凭证有效期</span>
        <span>{{"00:05:00"}}</span>
      </div>
    </div>
    <div class="acoount-compute-footor">
      <el-button @click="recharge">确定</el-button>
    </div>

    <el-dialog
      :title="dialogTitle"
      custom-class="chargeDialog"
      :visible.sync="dialogVisible"
      width="40%"
    >
      <template v-if="chargeStatus">
        <i class="el-icon-circle-check"></i>
        <span class="times" v-if="fileData.chargeType===0">续费：{{fileData.chargeValue}}次</span>
        <span v-else>续费：{{fileData.chargeValue}}</span>
      </template>
      <template v-if="!chargeStatus">
        <i class="el-icon-warning-outline"></i>
        <span class="times">请确认续费凭证有效期/正确性！</span>
      </template>
      <span slot="footer" class="dialog-footer">
        <el-button class="normal-button" @click="dialogVisible = false">知道了</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dialogTitle: '续费成功',
      dialogVisible: false,
      initModel: {},
      fileData: {
        chargeType: '',
        chargeValue: ''
      },
      chargeStatus: false
    }
  },
  methods: {
    getCompanyInfo() {
      this.ApiService.getCompanyInfo().then(res => {
        this.initModel = res.data
      })
    },
    tirggerFile(e) {
      this.uploadFile(e.target.files)
    },
    uploadFile(files) {
      var that = this
      var reader_onload = function(event) {
        var result = event.target.result
        var strArr = result.split('|')

        that.fileData.chargeType = +strArr[3]
        if (that.fileData.chargeType === 1) {
          that.fileData.chargeValue = that
            .moment(Number(strArr[2]) * 1000)
            .format('YYYY-MM-DD HH:mm:ss')
        } else {
          that.fileData.chargeValue = strArr[2]
        }
      }

      var reader_onerror = function(event) {
        console.log('readFileFailed')
      }

      Array.from(files).forEach(function(f) {
        var reader = new FileReader()
        reader.readAsText(f)

        reader.onload = reader_onload
        reader.onerror = reader_onerror
      })
    },
    recharge() {
      let file = document.getElementById('fileInput').files[0]
      if (!file) {
        this.Utils.message('请先上传凭证', 'warning')
        return
      }
      let form = new FormData()

      form.append('signFile', file)
      this.ApiService.recharge(form).then(res => {
        this.dialogVisible = true
        if (res.code === 0) {
          this.dialogTitle = '续费成功'
          this.chargeStatus = true
        } else {
          this.dialogTitle = '续费失败'
          this.chargeStatus = false
        }
      })
    }
  },
  mounted() {
    this.getCompanyInfo(fileInput)
  }
}
</script>


<style lang="less" scoped>
.acoount {
  height: 100%;
  // min-height: 750px;
  width: 100%;
  position: relative;
  .upload {
    width: 120px;
    height: 28px;
    cursor: pointer;
    display: inline-block;
    line-height: 27px;
    font-size: 12px;
    color: #3954ff;
    letter-spacing: 0;
    font-weight: bold;
    background: #ffffff;
    border: 1px solid #3954ff;
    border-radius: 2px;
    border-radius: 2px;
    text-align: center;
  }
  .account-top {
    margin-bottom: 20px;
  }
  .title {
    height: 50px;
    font-size: 14px;
    line-height: 50px;
    font-weight: bold;
    padding-left: 20px;
    border-bottom: 1px solid #ebeef9;
  }
  .acoount-content {
    // height: 600px;
    margin: 31px 20px;
    padding-bottom: 50px;
    .acoount-item {
      height: 120px;
      width: 100%;
      margin-bottom: 16px;
      background: #fbfcfd;
      border: 1px solid #ebeef9;
      border-radius: 5px;
      border-radius: 5px;
      padding: 22px 27px;
      span {
        display: block;
        &:nth-of-type(1) {
          font-size: 14px;
          color: #010953;
          letter-spacing: 0;
          font-weight: bold;
          margin-bottom: 22px;
        }
        &:nth-of-type(2) {
          font-family: Helvetica;
          font-size: 26px;
          color: #3145d1;
          letter-spacing: 0.93px;
          line-height: 32px;
        }
      }
    }
  }
  .acoount-compute-footor {
    height: 50px;
    line-height: 50px;
    padding: 0px 20px;
    border-top: 1px solid #ebeef9;
    position: absolute;
    bottom: 0px;
    width: 100%;
    text-align: right;
    .el-button {
      width: 96px;
    }
  }
}
</style>
<style lang="less">
.chargeDialog {
  .el-dialog__header {
    border-bottom: 1px solid #ebeef9;
    font-size: 14px;
    color: #333333;
    letter-spacing: 0;
  }
  .el-dialog__body {
    display: flex;
    align-items: center;
    flex-direction: column;
    font-size: 14px;
    color: #333333;
    height: 180px;
    letter-spacing: 0;
    font-weight: bold;
    .times {
      margin: 10px 0;
    }
    i {
      color: #3954ff;
      font-size: 40px;
      margin-top: 30px;
      margin-bottom: 10px;
    }
    .el-icon-warning-outline {
      color: #e02020;
      font-size: 40px;
    }
  }
  .el-dialog__footer {
    border-top: 1px solid #ebeef9;
    .el-button {
      width: 96px;
      height: 24px;
    }
  }
}
</style>